import React from "react";

import Typewriter from "typewriter-effect";

function Hi(){
    return (<div>
        <Typewriter options={{loop:true}}
                onInit={(typewriter) => {
                    typewriter
                        .typeString("Hello").pauseFor(1000)
                        .deleteAll()
                        .typeString("").pauseFor(1000)
                        .deleteAll()
                        .typeString("Bonjour").pauseFor(1000)
                        .deleteAll()
                        .typeString(" ").pauseFor(1000)
                        .deleteAll()
                        .typeString("").pauseFor(1000)
                        .deleteAll()
                        .typeString("").pauseFor(1000)
                        .deleteAll()
                        .typeString("").pauseFor(1000)
                        .deleteAll()

                        
                        
                        
                        .start();
                }}
 />
    </div>);
}

export default Hi;